document.addEventListener('DOMContentLoaded', ()=> {
    document.body.style.overflowX = 'hidden';

    const header = document.querySelector('.header');

    document.addEventListener('scroll', ()=> {
        headerBgToggler();
        
    });

    const headerBgToggler = function() {
        if(document.documentElement.scrollTop > 10) {
            header.classList.add('header_bg');
        } else {
            header.classList.remove('header_bg');
        }
    };

    const burger = document.querySelector('.header__burger');
    const lines = burger.querySelectorAll('span');
    const menu = document.querySelector('.menu');

    
    
    const scrollToggler = function() {
        if (menu.classList.contains('show_burger')) {
            document.body.style.overflowY = 'hidden';
        }
        if (!menu.classList.contains('show_burger')) {
            document.body.style.overflowY = 'scroll';
        }
            
    }

    burger.addEventListener('click', ()=> {
        mobileMenu();
        scrollToggler();
        
    });

    const mobileMenu = function() {
        menu.classList.toggle('show_burger');
        if(menu.classList.contains('show_burger')) {
            lines.forEach((i, num)=> {
                if(num == 0) {
                    i.style.cssText = 'transform: rotate(45deg); top: calc(50% - 2px);';
                }
                if(num == 1) {
                    i.style.cssText = i.style.cssText = 'opacity: 0';
                }
                if(num == 2) {
                    i.style.cssText = 'transform: rotate(-45deg); bottom: calc(50% - 2px);';
                }
            })
        } else {
            lines.forEach((i, num) => {
                if(num == 0) {
                    i.style.cssText = 'transform: rotate(0); ';
                }
                if(num == 1) {
                    i.style.cssText = 'opacity: 1;';
                }
                if (num == 2) {
                    i.style.cssText = 'transform: rotate(0);';
                }
            });
        }
    };

    scrollToggler();
});